header {
  	position: fixed;
    top: 0;
	left: 0;
	height: 75px;
	width: 100%;
	font-family: 'Lexend Deca', sans-serif;
	background: #000000;
	color: #FFFFFF;
	z-index:9;
	overflow:hidden;
	
	.logo {
		display: inline-block;
		float: left;
		margin:19px 0 26px 36px;

		.name {
			font-size: 24px;
			color:#EAEAEA;

			}		

		.role {
			font-size: 18px;
			color: #FFD439;
		}
	}

	&:hover, &:focus{
		.name {
			color:#FFFFFF;
			}		
		.role {
			color: #FFE073;
		}
	}
}

footer {
    bottom: 0;
	left: 0;
	height: 75px;
	background: #000000;
	color: #767676;
	padding-left:36px;
	z-index:9;
	clear: both;
	p{
	margin-top:0px;
	padding-top:24px;
	}
}
