.button {
	border-radius: 5px;
	border: none;
	color: #000000;
	height: 44px;
	width: 130px;
	text-align: center;
	vertical-align: middle;
	line-height: 43px;
	text-decoration: none;
	display: inline-block;
	font-size: 18px;
	font-family: 'Lexend Deca', sans-serif;
	margin-right: 16px;
	padding: none;

	a{
		color: #000000;
	}
}

.white{
		background-color: #F2F2F2;

		&:hover, &:focus{
		background-color: #EAEAEA;
		color: #000000;
		}

		&:active{
		background-color: #EAEAEA;
		color: #000000;
		}
	}	

.yellow{
	background-color: #FFD439;

		&:hover, &:focus{
		background-color: #FFC700;
		color: #000000;	
		}

		&:active{
			background-color: #FFC700;
			color: #000000;	
		}
	}	

.gohome{
	margin: 0px auto 50px auto;
	display: block;
	width:200px !important; 
}

.card{
	position: relative; 
	display: inline-block;
	max-width: 350px;
	border-radius: 5px;
	background-color: #ffffff;
	margin:0px 13px 50px 13px;
	padding-bottom: 8px;
	/* offset-x | offset-y | blur-radius | spread-radius | color */
	box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);

	img{
		border-radius: 5px 5px 0px 0px;
		width: 100%;
		display:block;
	    border-bottom: 1px solid #F2F2F2;
	    margin-top: 0px !important;

	}

	.tags{
		display:block;	
		margin: 10px 16px 0px 16px;
		font-family: 'Nunito', sans-serif;
		font-size:14px;
		color: #454545;
	}

	.title{
		display:block;
		margin: 8px 16px 0px 16px;
		font-family: 'Lexend Deca', sans-serif;
		font-weight: normal;
		font-size: 24px;
		color: #000000;
	}

	.description{
		display:block;	
		font-size: 16px;
		margin: 4px 16px 0px 16px;
		color: #000000;
	}

	span{
		color: #d7d7d7;
	}
}

.overlay{
	border-radius: 5px 5px 0px 0px;
	position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    z-index:1;
	background-color: #ffffff21;

    &:hover {
	background-color: #ffffff00;
	}
}

@media screen and (max-width: 1160px) {
.card{
	max-width: 600px;
	margin:0px auto 50px auto;
	text-align: center;

	}	
}

@media screen and (max-width: 620px) {
	.button {
		width: auto;
		padding-left: 16px;
		padding-right: 16px;
	}

	.card{
	max-width: 600px;
	margin:0px auto 36px auto;
	text-align: center;

	}

	.gohome{
	margin: 12px auto 50px auto;
	}
}