

#about{
	display: block;
	margin: 144px auto 70px auto; 
    max-width:929px;
}

#home_img{
	display: block;
	float: left;
	width: 366px;
	height: 366px;
	background-image: url('/assets/img/homepage_photo.png');
    background-position: center; /* Center the image */
    background-size: cover; /* Resize the background image to cover the entire container */		


}

#about_text {
	display: block;
	float: left;
	padding-left: 50px;
	max-width: 513px;
}
#hello{
	font-family: 'Lexend Deca', sans-serif;
	font-size: 48px;
}


#portolio_projects{
	position: relative;
	z-index:1;
	display: block;
	float: left;
	background-color: #f8f8f8;
	width: 100%;

	h2{
		color: black;
		text-align: center;	
		padding: 32px 0px 24px 0px;
		margin: 0px;

	}
}

.other_projects{
		background-color: #1B1B1B !important;
		h2{
				color: white !important;
			}
}
#cards{
	display: flex;
    justify-content: center;
}

/*Mobile Layout*/
@media screen and (max-width: 1160px) {

#about {
    display: block;
    margin: 75px auto 48px auto;
    max-width: 100%;
}

#about_text {
	display: block;
	float:none;
    margin: 0 auto 0 auto;
	padding-left: 0px;
	padding-top: 48+250px; 
	max-width: 513px;
}

#cards {
    width: 600px;
	margin: 12px auto 0 auto;
	display: block;	
    }

#home_img{
	display: block;
	float: left;
	width: 100%;
	height: 250px;
	background-image: url('/assets/img/homepage_photo_mobile.png');
	}
}

@media screen and (max-width: 620px) {
#cards {
    width: auto;
    margin: 12px 16px 0 16px;
    }

#about_text {
    margin: 0 16px 0 16px;
}
}

.mailto-wrapper {
	position: relative;
	}
.mailto-message {
	top: 0;
	left: 50%;
	transform: translate(-57%, -24px);
	margin-top: -30px;
	position: absolute;
	display: none;
	text-align: center;
	width: auto;
	white-space: nowrap;
	font-size: 16px;
	background-color: #000000E6;
	color: #FFD439;
	padding: 5px 16px;
	border-radius: 25px;
	&:after {
		content: '';
	}
}
.mailto-wrapper:hover .mailto-message {
	display: block;
}
