#hero_image {
	margin-top: 75px;
	max-height: 500px;
	height: 46vw;
	background-color: #1B1B1B;
	overflow: hidden;
    position: relative;
   -moz-box-shadow:      0 0 20px rgba(0,0,0,0.07);
   -webkit-box-shadow:  0 0 20px rgba(0,0,0,0.07);
    box-shadow:          0 0 20px rgba(0,0,0,0.07);
    background-position: center;
    background-size: cover;

	img{
		max-width:100%;
		vertical-align: middle;
		min-width:906px;
	    position: absolute;
	    top: -9934px;
	    bottom: -9999px;
	    left: -9999px;
	    right: -9999px;
	    margin: auto;

	}
}

#toc {
	float: left;
	font-family: 'Lexend Deca', sans-serif;
	padding-left: 24px;
    padding-top: 10px;
    padding-right: 30px;

	li{
		font-size: 18px;
		line-height: 43px;
		margin-top: 0px;
		margin-bottom: 0px;
		list-style-type:none
	}

	a {
	color: #aeaeae;
	text-decoration: none;
		&:focus, &:hover {
			color: #000000;
		}
		&:active{
			color: #FFB405;
		}
	}

		.active {
	    color: #000000;
	}
}

.sticky {
  position: fixed;
  top: 75px;
}

@media screen and (max-width: 1270px) {
#toc {display:none;}
}

#blog_content {
	max-width: 700px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 16px;
	padding-right:16px;
	padding-bottom: 48px;

	:target::before {
		content: "";
		display: block;
		height: 85px; /* fixed header height*/
		margin: -85px 0 0; /* negative fixed header height */
		}
	img {
		max-width:100%;
		margin-top: 10px;
/*		transform: scale(0.5, 0.5);
		-ms-transform: scale(0.5, 0.5);
		-webkit-transform: scale(0.5, 0.5);
		max-height:50%;*/

	}

	p{
		font-size: 16px;
		line-height: 26px;
		margin-top: 15px;
		margin-bottom: 0px;
		}

	li{
		font-size: 16px;
		line-height: 26px;
		margin-top: 10px;
		margin-bottom: 0px;
		}

	ul{
		list-style-type: disc;	
	}

	h1{
		font-size: 36px;
		margin-top: 30px;
		margin-bottom: 0px;	
	}

	h2{
		font-size: 24px;
		margin-top: 50px;
		margin-bottom: 0px;
	}

	h3{
		font-size: 18px;
		margin-top: 30px;
		margin-bottom: 0px;
	}

	.slash {
		color: #d7d7d7;
	}

}

#footer {
		background-color: #1B1B1B;
}
	
